.login_clinic{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 100vh;
    padding-top: 90px;
}
.login_clinic a{
    text-decoration: none;
}
.login_clinic a:hover{
    color: var(--dark-purple);
    cursor: pointer;
}
.login_form {
    display: flex;
width: 712px;
padding: 48px 72px;
flex-direction: column;
justify-content: center;
align-items: center;
border-radius: 8px;
box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}
.login_title{
    color: var(--light-dark);
font-family: Outfit;
font-size: 28px;
font-weight: 600;

}
.input_container {
    color: var(--medium-purple);
}
label{
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    color: var(--light-dark);
    width: 100%;
    margin: 0;
}
.pass{
    padding-top: 24px;
    margin: 0;
}
.forgot_a {
    text-align: center;
    color: var(--medium-purple);
    font-size: 16px;
    font-weight: 400;
}
.pass_forgot{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
   
}
form{
    display: flex;
    flex-direction: column;
    gap: 32px;
}
form .input_container{
    display: flex;
    flex-direction: column;
    width: 100%;
}
form input{
    display: flex;
    padding: 12px 16px;
align-items: center;
gap: 5px;
flex: 1 0 0;
align-self: stretch;
border-radius: 8px;
border: 1px solid var(--light-gray);
width: 560px;
font-size: 16px;
}
.button_login_clinic{
    display: flex;
height: 52px;
padding: 16px;
justify-content: center;
align-items: center;
align-self: stretch;
border-radius: 8px;
background-color: var(--medium-purple);
color: #fff;
font-weight: 600;
line-height: 100%;
border-color: var(--medium-purple);
cursor: pointer;
border:none;
}

.register_page span{
    display: flex;
    color: #BEBEBF;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 100%;
    text-transform: capitalize;
    align-items: center;
    justify-content: center;
    width: 100%;
}
.register_page .cad{
    color: var(--medium-purple);
}
.button_login_clinic button{
    font-size: 16px;
    background: transparent;
    border: none;
    color: #fff;
}
.button_login_clinic:hover{
    opacity: 0.8;

}
.register_page a{
    padding-left: 5px;
}
form .password-input {
    position: relative;
  }
  
form .password-input input {
    width: 100%;
    padding-right: 40px; /* Espaço para o ícone */
  }
  
form .eye-icon {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    background: none;
    border: none;
}
  .register_ii{
    display: flex;
    flex-direction: column;
    width: 50%;
    justify-content: center;
    align-items: center;
    gap: 60px;
  }
  .register_ii .registre{
    align-self: center;
  }
  .clinic_description{
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  textarea{
    min-height: 205px;
    color: #8D9396;
    background-color: #F5F5F5;
    border-radius: 6px;
    padding: 16px 32px 169px 16px;

  }
  .image-upload-container {
    position: relative;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    overflow: hidden;
    background-color: #f0f0f0; /* Cor de fundo para ilustração */
    
}

.image-upload-container input[type="file"] {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
   
}

.add-button {
    position: absolute;
    bottom: 10px;
    right: 10px;
    background-color:#B6B6F6;
    color: #fff;
    border: none;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    font-size: 20px;
    line-height: 30px;
    text-align: center;
    cursor: pointer;
    z-index: 20; /* Defina um índice Z para o botão */
}
@media(max-width:730px) {
    .login_form {
        width: 100%;
        box-shadow: none;
    }
    form input{
        width: 100%;
    }
.register_page span{
    flex-direction: column;
    justify-content: center;
    align-items: center ;
}
}




