@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Baloo+Da+2:wght@700&display=swap);
:root {
    --primary-color: #B6B6F6;
    --black: #444;
    --light-color: #777;
    --box-shadow: 0.5rem 0.5rem 0 rgba(27, 65, 57, 0.2);
    --text-shadow: 0.4rem 0.4rem 0 rgba(0, 0, 0, 0.2);
    --border: .2rem solid var(primary-color);
}

.header {
    padding: 1rem 10%;
    position: fixed;
    width: 100%;
    z-index: 1000;
    top: 0;
    box-shadow: 0 .5rem 1.5rem rgba(0, 0, 0, .1);
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #fff;
}

.header .logo {
    font-size: 36px;
    font-weight: 500;
    color: #B6B6F6;
    color: var(--primary-color);
    font-family: 'Baloo Da 2', cursive;
    text-decoration: none;
    color: #B6B6F6;
    color: var(--primary-color);
}

.logo-img {
    width: 50px;
}

#login-btn {
    font-size: 20px;
    color: #B6B6F6;
    color: var(--primary-color);
    padding: 0.3rem 2.5rem;
    border: 2px solid #B6B6F6;
    border: 2px solid var(--primary-color);
    border-radius: 50px;
    text-decoration: none;
    font-weight: 500;
}

#login-btn:hover {
    color: white;
    background-color: #B6B6F6;
    background-color: var(--primary-color);
    -webkit-transition: .5s;
    transition: .5s;
}

@media(max-width:1000px) {
    .header .logo {
        font-size: 30px;
    }

    .logo-img {
        width: 45px;
    }
}

@media(max-width:800px) {
    .header .logo {
        font-size: 25px;
    }

    .logo-img {
        width: 40px;
    }

    #login-btn {
        font-size: 15px;
        padding: 0.2rem 2rem;
    }
}

@media(max-width:500px) {
    .header .logo a {
        font-size: 20px;
    }

    .logo-img {
        width: 50px;
    }

    #login-btn {
        font-size: 20px;
        padding: 0.1rem 1rem;
    }
}
